@import url('/src/components/global.css');

.container-app {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  row-gap: 0; 
}

.container-app > *{
  border: 5px solid red;
}


@media(max-width:700px){
  .container-app > *{
    border: 5px solid green;
  }
}
